 import { Avatar } from "antd";
import "antd/dist/antd.css";
import { Anchor,Button } from 'antd';
import { Switch } from 'antd';
import { useContext, useState } from "react";
import {Link} from 'react-router-dom'
import { ThemeContext } from "../Context/themeContext";
import AnchorLink from 'react-anchor-link-smooth-scroll'

export const Navbar=()=>{
    const {handle}=useContext(ThemeContext)
    return <div id="Navbar">
         <div className="left"> 
         <Avatar size={45}>AS</Avatar>  
          </div>
         <div className="right">  
         <div> <Switch onChange={()=>{
                   handle()
                }} checkedChildren="Aqua" unCheckedChildren="Pink" defaultChecked /></div> 
            <div><AnchorLink offset='100' href="#Media">Contact</AnchorLink> </div>
            <div><AnchorLink offset={() => 80} href="#pro">Project</AnchorLink></div>
            <div><AnchorLink offset={() => 20} href="#SK">Skill</AnchorLink></div>
            <div><AnchorLink offset={() => 100} href="#Home">Home</AnchorLink></div>
            {/* <Anchor >
                <div className="right">
                <div><Link href="#Home" title="Home" /></div>
                <div> <Link href="#SK" title="Skill" /></div>
                <div> <Link href="#pro" title="Project" /></div>
                <div> <Link href="#Media" title="Contact" /></div>
                <Switch onChange={()=>{
                   handle()
                }} checkedChildren="Aqua" unCheckedChildren="Pink" defaultChecked />
                </div>
              </Anchor> */}

             
           </div>
          
               
    </div>
}